// <script>
  export default {
    props: {
      type: {
        type: String,
        default: 'info'
      }
    },
    computed: {
      boxStyle() {
        switch (this.type) {
          case 'warning':
            return 'bg-warning'
          case 'info':
            return 'bg-info'
          case 'success':
            return 'bg-success'
          case 'important':
            return 'bg-danger'
          default:
            return 'bg-info'
        }
      },

      iconType() {
        switch (this.type) {
          case 'warning':
            return 'glyphicon-warning-sign'
          case 'info':
            return 'glyphicon-warning-sign'
          case 'success':
            return 'glyphicon-ok'
          case 'important':
            return 'glyphicon-star'
          default:
            return 'glyphicon-warning-sign'
        }
      }
    }
  }
// </script>

